body {
  background-color: #25559D; }

a[accesskey] {
  color: #000000;
  text-decoration: none;
  font-size: 1.2rem; }

a[accesskey="U"] {
  position: fixed;
  top: 8px;
  left: 8px;
  color: #000; }

a:focus, button:focus {
  outline: 3px dashed #ff0073 !important; }

a[accesskey="C"] {
  scroll-margin-top: 100px;
  margin: 4px;
  color: #fff;
  z-index: 2; }

main {
  position: relative; }
  main a[accesskey="C"] {
    top: -30px; }

.btn-homepage-primary {
  background-color: #032C61;
  color: #fff;
  border-radius: 4px;
  padding: 8px 16px;
  text-decoration: none;
  font-size: 1rem; }
  .btn-homepage-primary:hover {
    background-color: #046B9F; }

.btn-homepage-secondary {
  background-color: #046B9F;
  color: #fff;
  border-radius: 4px;
  padding: 8px 16px;
  text-decoration: none;
  font-size: 1rem; }
  .btn-homepage-secondary:hover {
    background-color: #032C61; }

.btn-homepage-white {
  background-color: #fff;
  border: 1px solid #046B9F;
  color: #046B9F;
  border-radius: 4px;
  padding: 8px 16px;
  text-decoration: none;
  font-size: 1rem; }
  .btn-homepage-white:hover {
    background-color: #E4E4E4;
    color: #046B9F; }

.btn-burger {
  display: none;
  background-color: #032C61;
  width: 30px;
  aspect-ratio: 1/1;
  color: #fff;
  align-items: center;
  justify-content: center;
  border-radius: 50rem;
  outline: none;
  border: none;
  transition: .3s; }
  .btn-burger span {
    font-size: 1rem; }
  @media (max-width: 992px) {
    .btn-burger {
      display: flex; } }

.container {
  max-width: 1024px;
  margin: 0 auto; }

main {
  background: url(../img/homepage/bg-left-01.svg) no-repeat -90px 180px;
  background-size: 224px;
  min-height: 600px; }
  main:has(.page-section) {
    background: unset; }

.section-2 {
  background: url(../img/homepage/bg-right-01.svg) no-repeat right 0px bottom;
  background-size: 168px; }

.section-3 {
  background: url(../img/homepage/bg-left-02.svg) no-repeat left center, url(../img/homepage/section3-bg.svg) repeat-x center top;
  background-size: 140px, cover;
  margin-bottom: 60px; }

@media (min-width: 1440px) {
  .section-3 {
    background: url(../img/homepage/bg-left-02.svg) no-repeat left center, url(../img/homepage/sec3-bg-max-wave.svg) repeat-x center top;
    background-size: 140px, cover; } }

@media (max-width: 1024px) {
  main, .section-2 {
    background: unset; }
  .section-3 {
    background: url(../img/homepage/section3-bg.svg) repeat-x left -20px top;
    background-size: cover; } }

.header {
  background: rgba(255, 255, 255, 0.6);
